// Loading
.loading {
	@include loadingIndicator();

	min-height: $unit-4;
	pointer-events: none;
	position: relative;

	&.loading-lg {
		min-height: $unit-10;

		&::after {
			height: $unit-8;
			margin-left: -$unit-4;
			margin-top: -$unit-4;
			width: $unit-8;
		}
	}
}

.loading.container, .loading.column, form.loading {
	&:before {
		content: '';
		position: absolute;
		top: 0; left: 0; right: 0; bottom: 0;
		
		background:  rgba($light-color, .75);
		border-radius: inherit;
	}

	&:before, &:after {
		z-index: $zindex-1;
	}
}