@mixin loadingIndicator() {
    &::after {
        animation: loading 500ms infinite linear;
        border: $border-width-lg solid $primary-color;
        border-radius: 50%;
        border-right-color: transparent;
        border-top-color: transparent;
        content: "";
        display: block;
        height: $unit-4;
        left: 50%;
        margin-left: -$unit-2;
        margin-top: -$unit-2;
        position: absolute;
        top: 50%;
        width: $unit-4;
        z-index: $zindex-0;
    }
}