// This file is generated using gulp

@font-face {
    font-family: "rsui-icons";
    src: url($font-base + 'rsui-icons.eot?#iefix') format('eot'),
         url($font-base + 'rsui-icons.woff2') format('woff2'),
         url($font-base + 'rsui-icons.woff') format('woff'),
         url($font-base + 'rsui-icons.ttf') format('truetype'),
         url($font-base + 'rsui-icons.svg#rsui-icons') format('svg');

    font-weight: normal;
    font-style: normal;
}

%icon {
    display: inline-block;
    font-family: "rsui-icons";
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    vertical-align: middle;
}

.icon {
    @extend %icon;

    &.icon-2x { font-size: 2rem; }
    &.icon-3x { font-size: 3rem; }
    &.icon-4x { font-size: 4rem; }
}


